.header {
    background-color: #1C2B35;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 140px;
}

.header a {
    text-decoration: none;
    color: #fff;
    margin-left: 32px;
}

.header a:hover {
    color: orange;
}